import React from 'react'
import { useSelector } from 'react-redux'

const Load = () => {
    const on_load = useSelector(state => state.app.on_load)
    if(on_load){
    return (
        <div className="spinner-border text-info loading" role="status">
          <span className="sr-only"></span>
        </div>
    )
    }else{
        return null
    }
}

export default Load